.app {
  font-family: "Open Sans Condensed", sans-serif;
}

.product-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  grid-gap: 10px;
  grid-auto-rows: 330px;
}

.product-list img {
  width: 250px;
  height: 150px;
}

.image {
  height: 250px;
}

.icon {
  margin-right: 20px;
}

